import uniPopup from "../uni-popup/uni-popup.vue"
import {UserModel} from "../../model/user";
const  userModel = new UserModel()

export default {
  components: {
    uniPopup,
  },
  data() {
    return {
      showSignModel:false,
      showAssets:false,
      payShow:false,
    };
  },
    props:{
        user:{
            type:Object
        },
        userDisplayData : {
          type : Object
        }
    },
  methods: {
      _user(){
          uni.navigateTo({
              url:'/pagesA/personal/personal'
          })
      },
      _aboutUs(){
          uni.navigateTo({
              url:'/pagesA/about_us/about_us'
          })
      },
      _order(type){
          uni.navigateTo({
              url:'/pagesC/order/order?type='+type
          })
      },
      _icons(url){
        console.log(this.user)
          uni.navigateTo({
              url
          })
      },
      _delay(){
        uni.navigateTo({
          url:`/pagesA/delay_submit/delay_submit`
        })
      },
    switchSignModel(){
      this.showSignModel=!this.showSignModel
    },
    toBalance(){
      uni.navigateTo({
        url: "/pagesA/balance/balance"
      });
    },
    toRecharge(){
      uni.navigateTo({
        url: "/pagesA/recharge/recharge"
      });
    },
    toMyYuYue(){
      uni.navigateTo({
        url: "/pagesA/my_yuyue/my_yuyue"
      });
    },
    toIntegral(){
      uni.navigateTo({
        url: "/pagesA/integral/integral"
      })
    },
    toInvitation(){
      uni.navigateTo({
        url: "/pagesA/invitation/invitation"
      });
    },
    toViolation(){
      uni.navigateTo({
        url: "/pagesA/violation/violation"
      });
    },
    toMyAddress(){
      uni.navigateTo({
        url: "/pagesA/my_address/my_address"
      });
    },
    toMyCar(){
      uni.navigateTo({
        url: "/pagesA/my_car/my_car"
      });
    },
    toPayManage(){
      uni.navigateTo({
        url: "/pagesA/payment_manage/payment_manage"
      });
    },
    toMyInsurance(){
      uni.navigateTo({
        url:`/pagesA/my_insurance/my_insurance`
      })
    },
    toYearlyInspection(){
      uni.navigateTo({
        url:`/pagesA/yearly_inspection/yearly_inspection`
      })
    },
    bindgetuserinfo(res){
      console.log(res)
      this.$emit('bindgetuserinfo',res)
    }
  },
  
  onLoad: function() {
      console.log(2)
  },
  mounted(){
      
      console.log(1111)
  },
  onShow: function() {
      console.log(2)
  },
  onHide: function() {},
watch:{
    user:{
        handler(newValue, oldValue) {
            uni.setStorageSync('users',newValue)
           if(newValue.assets) {
               this.showAssets = true
           } else {
               this.showAssets = false
           }
        }
    },
    deep: true
}
};
